import React from 'react';
import Dialog from 'material-ui/Dialog';
import FlatButton from 'material-ui/FlatButton';
import Checkbox from 'material-ui/Checkbox';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import axios from 'axios'


export default class InformationModal extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            open: false,
            disabled: true,
            name: "",
        };
    }

    handleToggle = () => {
        this.setState({ disabled: !this.state.disabled })
    }


    render() {

        if (!this.props.userInfo){
            return null;
        }

        const actions = [
            <FlatButton
                label="Cancel"
                primary={true}
                onClick={this.props.closeAction}
            />,
            <FlatButton
                label="Submit"
                primary={true}
                disabled={this.state.disabled}
            // onClick={this.props.closeAction}
            />,
        ];


          let admin  = this.props.userInfo.map((adminsUser, index) => (
            // this.setState({name: adminsUser.firstName + " " + adminsUser.lastName})
                <div key={index} >
                <p> {adminsUser.firstName + " " + adminsUser.lastName}</p>
                    <p>{adminsUser.role + " " + adminsUser.notSuspended}</p>
                </div>
        ));

        console.log(this.props.userInfo) ; 

        return (
            <div>
                <MuiThemeProvider>
                    <Dialog
                        title={this.name}
                        actions={actions}
                        modal={true}
                        open={this.props.open}
                    >
                        <Checkbox
                            label="Suspend User"
                            onCheck={this.handleToggle}
                        />
                        {admin}
                    </Dialog>
                </MuiThemeProvider>
            </div>
        );
    }
}


